import React, { Component } from 'react';
import './App.css';
import Component2 from './Component2';

// UI framework component imports
import Button from 'muicss/lib/react/button';

export default class Screen3 extends Component {

  // Properties used by this component:
  // appActions, deviceInfo

  constructor(props) {
    super(props);
    
    this.state = {
    };
  }

  componentDidMount() {
    {
      let dataSheet = this.props.appActions.dataSheets['issues'];
      let serviceOptions = this.props.appActions.serviceOptions_issues;
      if ( !this.props.appActions.dataSheetLoaded['issues']) {
        serviceOptions.servicePath = dataSheet.expandSlotTemplateString("5mgja99r-aquatesters", this.props.appActions.dataSlots);
        this.props.appActions.loadData_gsheet2(dataSheet, serviceOptions, true);
        this.props.appActions.dataSheetLoaded['issues'] = true;
      }
      this._dataSheetLoadTimer_issues = setInterval(() => {  // Reload data regularly
        serviceOptions.servicePath = dataSheet.expandSlotTemplateString("5mgja99r-aquatesters", this.props.appActions.dataSlots);
        this.props.appActions.loadData_gsheet2(dataSheet, serviceOptions, false);
      }, 10000);
    }
  }

  componentWillUnmount() {
    clearInterval(this._dataSheetLoadTimer_issues);
  }

  componentDidUpdate() {
  }

  componentWillReceiveProps(nextProps) {
  }

  onClick_elButton = (ev) => {
    // Go to screen 'Start'
    this.props.appActions.goToScreen('start', { transitionId: 'fadeIn' });
  
  }
  
  
  onClick_elButton2 = (ev) => {
    // Go to screen 'Login 1'
    this.props.appActions.goToScreen('login1', { transitionId: 'fadeIn' });
  
  }
  
  
  render() {
    let layoutFlowStyle = {};
    let baseStyle = {};
    if (this.props.transitionId && this.props.transitionId.length > 0 && this.props.atTopOfScreenStack && this.props.transitionForward) {
      baseStyle.animation = '0.25s ease-in-out '+this.props.transitionId;
    }
    if ( !this.props.atTopOfScreenStack) {
      layoutFlowStyle.height = '100vh';
      layoutFlowStyle.overflow = 'hidden';
    }
    
    const dataSheet_issues = this.props.dataSheets['issues'];
    const style_elBackground = {
      width: '100%',
      height: '100%',
     };
    const style_elBackground_outer = {
      backgroundColor: '#eaeaea',
     };
    const style_elText = {
      fontSize: 42.8,
      color: 'rgba(0, 0, 0, 0.5000)',
      textAlign: 'center',
     };
    const style_elButton = {
      display: 'block',
      fontSize: 19.0,
      color: 'white',
      textAlign: 'center',
      cursor: 'pointer',
      pointerEvents: 'auto',
     };
    const style_elList = {
      height: 'auto',  // This element is in scroll flow
     };
    
    // Source items and any special components used for list/grid element 'list'.
    let items_list = [];
    let listComps_list = {};
    items_list = items_list.concat(this.props.appActions.getDataSheet('issues').items);
    
    const style_elButton2 = {
      display: 'block',
      color: 'white',
      textAlign: 'center',
      cursor: 'pointer',
      pointerEvents: 'auto',
     };
    
    return (
      <div className="AppScreen Screen3" style={baseStyle}>
        <div className="background">
          <div className="containerMinHeight elBackground" style={style_elBackground_outer}>
            <div style={style_elBackground} />
          </div>
        </div>
        
        <div className="layoutFlow" style={layoutFlowStyle}>
          <div className="elText">
            <div className="systemFontBold" style={style_elText}>
              <div>{this.props.locStrings.screen3_text_809617}</div>
            </div>
          </div>
          
          <div className="elButton">
            <Button className="systemFontRegular" style={style_elButton}  color="accent" onClick={this.onClick_elButton} >
              {this.props.locStrings.screen3_button_404519}
            </Button>
          </div>
          
          <div className="hasNestedComps elList">
            <ul style={style_elList}>
              {items_list.map((row, index) => {
                let itemComp = (row._componentId)
                    ? listComps_list[row._componentId]
                    : <Component2 dataSheetId={'issues'} dataSheetRow={row} {...{ 'screenshoturl': row['screenshoturl'], 'appname': row['appname'], 'issue': row['issue'], 'status': row['status'], 'date': row['date'], 'username': row['username'], }} appActions={this.props.appActions} deviceInfo={this.props.deviceInfo} locStrings={this.props.locStrings} />;
                return (<li key={row.key}>
                    {itemComp}
                  </li>);
              })}
              <div className="marker" ref={(el)=> this._elList_endMarker = el} />
            </ul>
          </div>
          
          <div className="elButton2">
            <Button className="actionFont" style={style_elButton2}  color="accent" onClick={this.onClick_elButton2} >
              {this.props.locStrings.screen3_button2_338870}
            </Button>
          </div>
          
          <div className="elSpacer">
            <div />
          </div>
        </div>
        
      </div>
    )
  }
  
}
